import { Component, Renderer } from '@angular/core';
import { 
  IonicPage,
  NavController, 
  ModalController, 
  NavParams, 
  ViewController, 
  Platform,
  MenuController
} from 'ionic-angular';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ToolsProvider } from '../../providers/tools/tools';
import { LoginProvider } from '../../providers/login/login';

@IonicPage()
@Component({
  selector: 'page-register',
  templateUrl: 'register.html',
})
export class RegisterPage {
  private formValid: FormGroup;
  private dataValid:any ={};
  private errClientId: string='';
  private errNik: string='';
  private errKtp: string='';
  private errTglLahir: string='';
  private validate: boolean=false;
  private ipAdr:any;

  constructor(
    public navCtrl: NavController, 
    public mdlCtrl: ModalController,
    public fb: FormBuilder,
    public platform: Platform,
    public lgn: LoginProvider,
    public tool: ToolsProvider    ,
    public menu: MenuController
  ) {
    this.menu.enable(false);
    this.formValid = this.fb.group({
      clientId:['',Validators.compose([Validators.required, Validators.minLength(4), Validators.maxLength(4)])],
      nik:['', Validators.required],
      ktp:['', Validators.required],
      tglLahir:['', Validators.required]
    });
  }

  ionViewDidLoad(){
    this.platform.ready().then(()=>{
      let dsc = this.lgn.showSK(); 
      dsc.present();
      this.tool.getStg('ip').then(
        (data)=>{
          if(data){
            this.ipAdr = data.ip;
            // console.log('IP addr',this.ipAdr);
          }
        }
      );
    });
  }

  goBack(){
    this.navCtrl.pop();
  }

  doValidasi(){
    this.tool.showLoading();
    this.tool.getStg('net').then(
      data => {
        if(data.conn!="offline"){
          this.validasi();
        }else{
          this.tool.showAlert('Error','tidak ditemukan jaringan internet');
          this.tool.stopLoading();
        }
      },
      error=>{
        console.log('Register->doValidasi()',error);
        this.tool.stopLoading();
      }
    );
  }

  validasi(){
      this.lgn.getValidasi(this.formValid.value.clientId).subscribe(
        data=>{
          // console.log(data);
          this.tool.stopLoading();
          let res: any;
          res = data;
          this.dataValid = data;
          if(res.result == '1'){
            this.errClientId = '';
            let ok: boolean= true;
            this.validate = true;
            if(!(res.nik == this.formValid.value.nik)){
              this.lgn.logger(this.formValid.value.clientId,'Mencoba validasi tapi nik salah', this.ipAdr).subscribe();                    
              this.errNik = 'Data Nomor Induk Karyawan tidak sesuai';
              ok = false;
            }
            if(!(res.ktp == this.formValid.value.ktp)){
              this.lgn.logger(this.formValid.value.clientId,'Mencoba validasi tapi tapi ktp salah', this.ipAdr).subscribe();  
              this.errKtp = 'Data Nomor Identitas(KTP) tidak sesuai';
              ok = false;
            }
            if(!(res.tglLahir == this.formValid.value.tglLahir)){
              this.lgn.logger(this.formValid.value.clientId,'Mencoba validasi tapi tapi tglLahir salah', this.ipAdr).subscribe();  
              this.errTglLahir = 'Data Tanggal Lahir tidak sesuai';
              ok = false;
            }
            if(ok){
              this.lgn.logger(this.formValid.value.clientId,'Berhasil Validasi', this.ipAdr).subscribe();  
              this.validate = false; 
              this.openModal(res.clientId, res.nama);
            }
          }else{
            this.lgn.logger(this.formValid.value.clientId,'Mencoba validasi tapi data tidak ada', this.ipAdr).subscribe();                    
            this.errClientId = "tidak ada Kode Nasabah ["+this.formValid.value.clientId+"]";
            this.errKtp = '';
            this.errNik = '';
            this.errTglLahir = '';
          }
        },
        error=>{
          console.log('Register->validasi()',error);  
          this.tool.stopLoading();        
        }
      );
  }

  checkInput(){
    if(this.dataValid.nik == this.formValid.value.nik){
      this.errNik = '';
    }else{
      if(this.validate){
        this.errNik = 'Data Nomor Induk Karyawan tidak sesuai';        
      }
    }
    if(this.dataValid.ktp == this.formValid.value.ktp){
      this.errKtp = '';
    }else{
      if(this.validate){
        this.errKtp = 'Data Nomor Identitas(KTP) tidak sesuai';        
      }
    }
    if(this.dataValid.tglLahir == this.formValid.value.tglLahir){
      this.errTglLahir = '';
    }else{
      if(this.validate){
        this.errTglLahir = 'Data Tanggal Lahir tidak sesuai';        
      }
    }
  }



  openModal(clientId:string, nama:string){
    let modal = this.mdlCtrl.create(ShowModal,{clientId:clientId, nama:nama},{enableBackdropDismiss:false});
    modal.present();
    modal.onDidDismiss((res)=>{
      if(res){
        this.navCtrl.popTo('HomePage');
      }
    });
    if(this.platform.is('android')&& !this.platform.is('mobileweb')){
      this.navCtrl.popTo('HomePage');
    }
  }

}

//==================================================================================================================================================
//   MODAL
//==================================================================================================================================================
@Component({
  selector: 'page-modal',
  templateUrl: 'modal.html'  
})
export class ShowModal{
  private disclaim:boolean = false;
  private clientId:string = '';
  private pass:string = '';
  private conf:string = '';
  private nama:string = '';

  private color: string='';
  private strength: string='';
  
  private strong = new RegExp("^(?=.*[a-zA-Z])(?=.*[0-9])(?=.{8,})");
  private medium = new RegExp("^(?=.*[a-zA-Z])(?=.*[0-9])(?=.{5,})");
  private weak = new RegExp("^((?=.*[0-9])|(?=.*[a-zA-Z]))(?=.{1,})");

  private ipAdr:any;
  
  constructor(
    public render: Renderer,
    public viewCtdl: ViewController,
    public params: NavParams,
    public tool: ToolsProvider,
    public lgn: LoginProvider,
    public platform: Platform
  ){
    this.render.setElementClass(viewCtdl.pageRef().nativeElement, 'my-popup',true);
    this.clientId = params.get('clientId');
    this.nama = params.get('nama');
    console.log(this.conf);
  }

  ionViewDidLoad() {
    this.platform.ready().then(()=>{
      this.tool.getStg('ip').then(
        (data)=>{
          if(data){
            this.ipAdr = data.ip;
            // console.log('IP addr',this.ipAdr);
          }
        }
      );
      this.tool.showRegisInfo();
    });
  }

  daftar(){
    this.tool.showLoading();
    this.tool.getStg('net').then(
      data=>{
        if(data.conn!="offline"){
          this.simpan();        
        }else{
          this.tool.showAlert('Error','tidak ditemukan jaringan internet');
          this.tool.stopLoading();
        }
      },
      error=>{
        console.log('Register->daftar()',error);
        this.tool.stopLoading();
      }
    );
  }

  simpan(){
    this.lgn.createLogin(this.clientId, this.pass, this.nama).subscribe(
      data=>{
        this.tool.stopLoading();
        if(data.result== '1'){
          this.lgn.logger(this.clientId,'Berhasil Mendaftar', this.ipAdr).subscribe();            
          this.tool.showAlert('Berhasil',data.message);
          this.dismiss(true);
        }else{
          this.lgn.logger(this.clientId,'Mencoba mendaftar tapi data sudah ada', this.ipAdr).subscribe();
          let conf = this.tool.showConfirm('Anda sudah terdaftar ,<br>Apakah anda ingin mengubah password?','Konfirmasi','Tidak', 'YA');
          conf.present();
          conf.onDidDismiss((res)=>{
            if(res){             
              this.tool.showLoading();
              this.ubah();
            }else{          
              this.lgn.logger(this.clientId,'Batal ubah data', this.ipAdr).subscribe();
            }
          });
        }
      },
      error=>{
        console.log('Register->simpan()',error);
        this.tool.stopLoading();
      }
    );
  }

  ubah(){
    this.lgn.updateLogin(this.clientId, this.pass, this.nama).subscribe(
      data=>{
        this.tool.stopLoading();        
        if(data.result=='1'){
          this.lgn.logger(this.clientId,'Berhasil ubah data', this.ipAdr).subscribe();
          this.tool.showAlert('Berhasil',data.message); 
          this.dismiss(true);  
        }else{
          this.tool.showAlert('Gagal',data.message);
        }
      },
      error=>{
        console.log('Register->ubah()',error);
      }
    );
  }

  policy(){
    if(this.disclaim){
      let dsc = this.lgn.showSK(); 
      dsc.present();
      dsc.onDidDismiss((res)=>{
        this.disclaim = res;
        if(res){
          this.lgn.logger(this.clientId,'Setuju Disclaimer', this.ipAdr).subscribe();
        }else{
          this.lgn.logger(this.clientId,'Tidak Setuju Disclaimer', this.ipAdr).subscribe();
        }
      });
    }
  }

  showSK(){
    let dsc = this.lgn.showSK(); 
    dsc.present();
  }

  showDC(){
    let dsc = this.lgn.showDisclaimer(); 
    dsc.present();
  }

  info(){
    this.tool.showRegisInfo();
  }

  passInput(input:string){
    if(this.strong.test(input)){
      this.strength = 'kuat';
      this.color = 'secondary';
    }else if(this.medium.test(input)){
      this.strength = 'sedang';
      this.color = 'warning';
    }else if(this.weak.test(input)){
      this.strength = 'lemah'
      this.color = 'danger';
    }else{
      this.strength = '';
      this.color = 'light';
    }
  }

  dismiss(ret: any){
    this.viewCtdl.dismiss(ret);
  }

}




