import { Component, Renderer } from '@angular/core';
import { ViewController } from 'ionic-angular';
import { MarketProvider } from '../../providers/market/market';
import { ToolsProvider } from '../../providers/tools/tools';


@Component({
  selector: 'dscl',
  templateUrl: 'dscl.html'
})
export class DsclComponent {

  public disclaimer: any;

  constructor(
    public tools: ToolsProvider,    
    public viewCtrl: ViewController,
    public render: Renderer,
    public mkt: MarketProvider,
  ) {

    this.tools.getStg('dscl').then(
      res=>{
        if(res){
          this.disclaimer = res;
        }else{
          this.getDisclaim();
        }
      },err=>{
        this.getDisclaim();
      }
    );
    this.render.setElementClass(viewCtrl.pageRef().nativeElement, 'my-disclaim',true);    
  }

  getDisclaim(){
    this.mkt.getDisclalimWeb().subscribe(
      data=>{
        // console.log(data);
        this.tools.setStg('dscl',data);
        this.disclaimer = data;
      }
    );
  }

  dismiss(val: boolean){
    this.viewCtrl.dismiss(val);
  }

}



