import { Component, ViewChild } from '@angular/core';
import { Nav, Platform, Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Network } from '@ionic-native/network';
import { ToolsProvider } from '../providers/tools/tools';
import { AppMinimize } from '@ionic-native/app-minimize';
import { LoginProvider } from '../providers/login/login';
import { AppVersion } from '@ionic-native/app-version';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage: any = 'LoginPage';
  version: any;
  pages: Array<{title: string, component: any, icon:any}>;
  clientId:any;
  nama: any;

  ipAdr:any;

  constructor(
    public platform: Platform, 
    public statusBar: StatusBar, 
    public splashScreen: SplashScreen,
    public net: Network,
    public ev:Events,
    public tool: ToolsProvider,
    public appMinimize: AppMinimize,
    public lgn: LoginProvider,
    public ver: AppVersion
  ) {
    this.initializeApp();
    this.tool.setStg('net',{conn: null});
  }

  setPage(user){
    if(user){
      this.clientId = user.clientId;
      this.nama = user.nama;
      if(user.level === '1'){
        if(user.clientId==='Admin'){
          this.pages = [
            { title: 'Penjualan BBCA', component: 'AdminPage', icon:'cart' },
            { title: 'Buat User', component: 'CreateUserPage', icon:'people' }
          ];
        }else{
          this.pages = [
            { title: 'Penjualan BBCA', component: 'AdminPage', icon:'cart' }
          ];
        }
      }else{
        this.pages = [
          { title: 'Formulir Penjualan', component: 'HomePage', icon:'cart' },
          { title: 'Histori Pengiriman Formulir', component: 'HistoryPage', icon: 'list-box' },
          { title: 'Syarat dan Ketentuan', component: 'SyaratPage', icon: 'information-circle' },
          { title: 'Disclaimer', component: 'KeteranganPage', icon: 'information-circle' }
        ];
      }
    }
  }

  initializeApp() {
    
    this.platform.ready().then(() => {

      this.platform.registerBackButtonAction(()=>{
        this.appMinimize.minimize();
      });

      this.ev.subscribe('user',(user)=>{  
        this.setPage(user);
      });

      this.tool.getStg('login').then(
        data=>{
          this.setPage(data);
        }, 
        error=>{
          console.log('getStgLogin', error);
        }
      );

      this.checkUpdate();
      this.getIp();
      this.subscribeNetwork();
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  checkUpdate(){
    if(this.platform.is("android")&& !this.platform.is('mobileweb')){
      this.ver.getVersionNumber().then(
        data=>{
          this.version = data
          this.lgn.getData('version','123').subscribe(
            data=>{
              if (this.version < data.nama){
                let conf = this.tool.showConfirm('Versi terbaru untuk aplikasi ini telah tersedia <br><br>Versi saat ini:'+this.version+' <br>Versi Terbaru:'+data.nama,'Pembaruan','Lewati', 'Unduh');
                conf.present();
                conf.onDidDismiss((res)=>{
                  if(res){             
                    window.open('https://play.google.com/store/apps/details?id=bcas.it5.formbbca');
                  }
                });
              }
            }
          );
        }
      );
    }
  }

  subscribeNetwork(){
    this.net.onConnect().subscribe(data => {
      this.tool.setStg('net',{conn: data.type});    
      //this.tool.showToast('koneksi internet anda saat ini: '+data.type);      
    }, error => console.error(error));
    this.net.onDisconnect().subscribe(data => {
      this.tool.setStg('net',{conn: data.type}); 
      this.tool.showToast('koneksi internet anda saat ini: '+data.type);      
    }, error => console.error(error));
  }

  getIp(){
    this.lgn.getIp().subscribe(
      data=>{
        // console.log('get ip',data);
        let res: any;
        res = data;
        this.ipAdr = res.ip;
        this.tool.setStg('ip',data).then();
      },
      err=>{
        console.log('error', err);
      }
    );
  }

  openPage(page) {
    this.nav.setRoot(page.component);
  }

  logout(){
    this.lgn.logger(this.clientId,'Berhasil Logout',this.ipAdr).subscribe();
    this.tool.clearStg();
    this.lgn.getIp().subscribe(
      data=>{
        // console.log('get ip',data);
        let res: any;
        res = data;
        this.ipAdr = res.ip;
        this.tool.setStg('ip',data).then(()=>{
          this.nav.setRoot('LoginPage');
        });
      },
      err=>{
        console.log('error', err);
      }
    );
  }
}



