import { Http, Headers } from '@angular/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import { Login } from '../../models/login';
import { ToolsProvider } from '../tools/tools';
import { Validasi } from '../../models/validasi';
import { DbProc } from '../../models/dbproc';
import { Holiday } from '../../models/holiday';
import { ModalController } from 'ionic-angular';
import { DsclComponent } from '../../components/dscl/dscl';
import { SdktComponent } from '../../components/sdkt/sdkt';

@Injectable()
export class LoginProvider {

  // private apiUrl = 'http://localhost/api';
  // private ipurl = 'http://freegeoip.net/json/';
  private ipurl = 'https://freegeoip.net/json/';
  private apiUrl = 'https://eformbbca.bcasekuritas.co.id/api';
  //private apiUrl = 'https://kangpanci.com/api';

  constructor(
    public tool: ToolsProvider,
    public http: Http,
    public mdlCtrl: ModalController
  ) {
    
  }

  _getHeaders() {
    let headers = new Headers();    
    headers.append('Content-Type', 'x-www-form-urlencoded');
    return headers;
  }

  getData(clientId: string, password: string): Observable<Login>{
    let headers = this._getHeaders();
    let data = {
      clientId: clientId,
      password: password
    }
    return this.http.post(`${this.apiUrl}/login/read_one.php`,data,{headers})
    .map(res => <Login>(res.json()));
  }

  getValidasi(clientId:string): Observable<Validasi> {
    return this.http.get(`${this.apiUrl}/validasi/read_one.php?clientId=${clientId}`)
      .map(res => <Validasi>(res.json()));
  }

  getIp(): Observable<DbProc>{
    // console.log('req ip');
    return this.http.get(`${this.ipurl}`)
    .map(res=> <DbProc>(res.json()));
  }

  createLogin(cId, pass, nama): Observable<DbProc>{
    let headers = this._getHeaders();
    return this.http.post(
        `${this.apiUrl}/login/create.php`,
        {
          clientId: cId,
          password: pass,
          nama: nama,
          level: '0'
        },
        {headers}
      ).map(res=><DbProc>(res.json()));
  }

  createAdmin(cId, pass, nama): Observable<DbProc>{
    let headers = this._getHeaders();
    return this.http.post(
        `${this.apiUrl}/login/create.php`,
        {
          clientId: cId,
          password: pass,
          nama: nama,
          level: '1'
        },
        {headers}
      ).map(res=><DbProc>(res.json()));
  }
  
  updateLogin(cId, pass, nama): Observable<DbProc>{
    let headers = this._getHeaders();
    let data = {
      clientId: cId,
      password: pass,
      nama: nama,
      level: '0'
    }
    let url = `${this.apiUrl}/login/update.php`;
    return this.http.post(url,data,{headers}).map(res=><DbProc>(res.json()));
  }

  updateAdmin(cId, pass, nama): Observable<DbProc>{
    let headers = this._getHeaders();
    let data = {
      clientId: cId,
      password: pass,
      nama: nama,
      level: '1'
    }
    let url = `${this.apiUrl}/login/update.php`;
    return this.http.post(url,data,{headers}).map(res=><DbProc>(res.json()));
  }

  logger(user, log, ip):Observable<DbProc>{
    if(!ip) ip='12345678';
    let headers = this._getHeaders();
    return this.http.post(
        `${this.apiUrl}/logger/create.php`,
        {
          user: user,
          log: log,
          ip: ip
        },
        {headers}
      ).map(res=><DbProc>(res.json()));
  }

  getHoliday(tgl:string):Observable<Holiday>{
    // console.log('getHoliday: ',tgl);
    return this.http.get( `${this.apiUrl}/holiday/read_one.php?tanggal=${tgl}`)
    .map(res=><Holiday>(res.json()));
  }

  showDisclaimer(){
    let modal = this.mdlCtrl.create(DsclComponent,{},{enableBackdropDismiss:false});
    return modal;
  }

  showSK(){
    let modal = this.mdlCtrl.create(SdktComponent,{},{enableBackdropDismiss:false});
    return modal;
  }

}



