import { Injectable } from '@angular/core';
import { LoadingController, AlertController, Platform, ToastController, ModalController } from 'ionic-angular';
import { NativeStorage } from '@ionic-native/native-storage';
import { Storage } from '@ionic/storage';
import { DscmPage } from '../../pages/dscm/dscm';

@Injectable()
export class ToolsProvider {

  private loading: any;

  constructor(
    public loadCtrl: LoadingController,
    public alrt: AlertController,
    public stg: Storage,
    public nStg: NativeStorage,
    public platform: Platform,
    public toastCtrl: ToastController,
    public mdlCtrl: ModalController
  ) {
  }

  showLoading(){
    this.loading = this.loadCtrl.create({
      spinner: 'ios',
      content: 'Mohon Tunggu ...',
      cssClass: 'csload'
    });
    this.loading.present();
  }
  stopLoading(){
    this.loading.dismiss();
  }

  showAlert(judul:string, pesan:string){
    let alert = this.alrt.create({
      title: '<center>'+judul+'</center>',
      message: '<center>'+pesan+'</center>',
      buttons:['Ok']
    });
    alert.present();
  }

  setAlert(judul:string, pesan:string){
    let alert = this.alrt.create({
      title: '<center>'+judul+'</center>',
      message: '<center>'+pesan+'</center>',
      buttons: [{
        text: 'ok',
        handler: ()=>{
          alert.dismiss(true);
          return false;          
        }
      }]
    });
    return alert;
  }


  showConfirm(msg: string, title: string, cc: string, ok:string): any{
    let alert = this.alrt.create({
      title: title,
      message: msg,
      buttons: [{
        text: cc,
        role: 'cancel',
        handler: () => {
          alert.dismiss(false);
          return false;
        }
      },{
        text: ok,
        handler: ()=>{
          alert.dismiss(true);
          return false;          
        }
      }]
    });
    return alert;
  }

  showRegisInfo(){
    let alert = this.alrt.create({
      title: 'Info',
      message: "<ul><li>Isikan Password minimum 8 karakter (alphanumeric)</li><li>Password dan Konfirmasi Password harus sama</li><li>Apabila lupa Password mohon melakukan registrasi ulang</li></ul>",
      buttons: ['OK']
    });
    alert.present();
  }

  showToast(pesan: string){
    let toast = this.toastCtrl.create({
      message: pesan,
      position: 'middle',
      duration: 1500,
      cssClass: 'toastPunya',
    });
    toast.onDidDismiss(() => {
    });
    toast.present();
  }

  getPlatform():string{
    let res: string;
    if(this.platform.is('android')&& !this.platform.is('mobileweb')){
      res = "1";
    }else if(this.platform.is('core') || this.platform.is('mobile') || this.platform.is('mobileweb')){
      res = "2";
    }
    return res;
  }

  getStg(val:string){
    if(this.getPlatform()==="1"){
      return this.nStg.getItem(val);
    }else if(this.getPlatform() === "2"){
      return this.stg.get(val); 
    }
  }

  setStg(id:string, val: any){
    if(this.getPlatform()==="1"){
      return this.nStg.setItem(id,val);
    }else if(this.getPlatform() === "2"){
      return this.stg.set(id,val); 
    }
  }

  clearStg(){
    this.nStg.clear();
    this.stg.clear();
    this.stg.set('login',null);
    this.stg.set('sdkt',null);
  }

  showModal(){
    let modal = this.mdlCtrl.create(DscmPage,{},{enableBackdropDismiss:false});
    return modal;
  }



}



