import { Component, Renderer } from '@angular/core';
import { MarketProvider } from '../../providers/market/market';
import { ToolsProvider } from '../../providers/tools/tools';
import { ViewController } from 'ionic-angular';

/**
 * Generated class for the SdktComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'sdkt',
  templateUrl: 'sdkt.html'
})
export class SdktComponent {

  public syarat:any;

  constructor(
    public mkt: MarketProvider,
    public tools: ToolsProvider,
    public viewCtrl: ViewController,
    public render: Renderer
  ) {
    this.tools.getStg('sdkt').then(
      res=>{
        if(res){
          // console.log('res=>',res);
          this.syarat = res;
        }else{
          this.getSK();
        }
      },err=>{
        this.getSK();
      }
    );
    this.render.setElementClass(viewCtrl.pageRef().nativeElement, 'my-disclaim',true); 
  }

  getSK(){
    this.mkt.getAturanWeb().subscribe(
      data=>{
        // console.log(data);
        this.tools.setStg('sdkt',data);
        this.syarat = data;
      },err=>{
        console.log('getSKErr',err);
      }
    );
  }

  dismiss(val: boolean){
    this.viewCtrl.dismiss(val);
  }


}



