import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { ShowModal } from '../pages/register/register';
import { HttpModule } from '@angular/http';
import { Network } from '@ionic-native/network';
import { NativeStorage } from '@ionic-native/native-storage';
import { IonicStorageModule } from '@ionic/storage';
import { ToolsProvider } from '../providers/tools/tools';
import { LoginProvider } from '../providers/login/login';
import { MarketProvider } from '../providers/market/market';
import { DscmPage } from '../pages/dscm/dscm';
import { AppMinimize } from '@ionic-native/app-minimize';
import { AppVersion } from '@ionic-native/app-version';
import { DsclComponent } from '../components/dscl/dscl';
import { SdktComponent } from '../components/sdkt/sdkt';

@NgModule({
  declarations: [
    MyApp,
    ShowModal,
    DscmPage,
    DsclComponent,
    SdktComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp,{mode:"md"}),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    ShowModal,
    DscmPage,
    DsclComponent,
    SdktComponent
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Network,
    NativeStorage,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    ToolsProvider,
    LoginProvider,
    MarketProvider,
    AppMinimize,
    AppVersion
  ]
})
export class AppModule {}



