import { Http, Headers } from '@angular/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Rx';
import 'rxjs/add/operator/map';
import { Bbca } from '../../models/bbca';
import { DbProc } from '../../models/dbproc';
import { Hist } from '../../models/hist';
import { Admin } from '../../models/admin';

@Injectable()
export class MarketProvider {

  // private apiUrl = 'http://localhost/api';
  private apiUrl = 'https://eformbbca.bcasekuritas.co.id/api';
  // private apiUrl = 'https://kangpanci.com/api';

  constructor(public http: Http) {
  }

  _getHeaders() {
    let headers = new Headers();    
    headers.append('Content-Type', 'x-www-form-urlencoded');
    return headers;
  }

  jual(id: string, jml:string, hrg:string): Observable<DbProc>{
    let headers = this._getHeaders();
    let data = {
      clientId:id,
      jumlah:jml,
      harga:hrg,
      tglJual:'20180101',
      status:'sent',
    };
    return this.http.post(
      `${this.apiUrl}/jual/create.php`,data,{headers}
    ).map(res=><DbProc>(res.json()));
  }

  getBBCA(): Observable<Bbca>{
    return this.http.get(`${this.apiUrl}/stock/read_one.php?kode=BBCA`)
    .map(res=><Bbca>(res.json()));
  }

  getData(clientId): Observable<Hist[]>{
    return this.http.get(`${this.apiUrl}/jual/read.php?clientId=${clientId}`)
    .map(res => <Hist[]>res.json());
  }


  getAdminData(tglSrc): Observable<Admin[]>{
    return this.http.get(`${this.apiUrl}/jual/admin.php?tglJual=${tglSrc}`)
    .map(res=><Admin[]>res.json().records);
  }

  getOddLot(tglSrc){
    this.http.get(`${this.apiUrl}/jual/oddlot.php?tglJual=${tglSrc}`).subscribe(data=>{
      window.open(data.url);
    });
  }

  getFixLot(tglSrc){
    this.http.get(`${this.apiUrl}/jual/fixlot.php?tglJual=${tglSrc}`).subscribe(data=>{
      window.open(data.url);
    });
  }

  getDisclalimWeb(){
    return this.http.get(`${this.apiUrl}/disclaimer/read.php`)
    .map(res=>res.json().records);
  }

  getAturanWeb(){
    return this.http.get(`${this.apiUrl}/rule/read.php`)
    .map(res=>res.json().records);
  }

  getKeterangan(){
    return this.http.get(`${this.apiUrl}/keterangan/read.php`)
    .map(res=>res.json().records);
  }
  

}



