import { Component, Renderer } from '@angular/core';
import { NavController, NavParams, ViewController, Platform } from 'ionic-angular';
import { MarketProvider } from '../../providers/market/market';

@Component({
  selector: 'page-dscm',
  templateUrl: 'dscm.html',
})
export class DscmPage {

  syarat: any[];
  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public viewCtrl: ViewController,
    public render: Renderer,
    public mkt: MarketProvider,
    public platform: Platform
  ) {
    this.render.setElementClass(viewCtrl.pageRef().nativeElement, 'my-disclaim',true);
  }

  ionViewDidLoad() {
    this.platform.ready().then(()=>{
      this.mkt.getAturanWeb().subscribe(
        data=>{
          console.log(data);
          this.syarat = data;
        }
      );
    });
  }

  dismiss(val: boolean){
    this.viewCtrl.dismiss(val);
  }

}



